import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'
import SurveyList from '@/components/SurveyList'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    },
    {
      path: '/surveylist',
      name: 'SurveyList',
      component: SurveyList
    }
  ],
  mode: 'history'
})



